import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Button } from "../components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "../components/ui/card";
import { Badge } from "../components/ui/badge";
import BottomTabs from "../components/BottomTabs";

// shape coming back from /api/history/list
export interface HistoryEntry {
  id: string;
  user_id: string;
  prompt: string;
  response: string;
  created_at: string;
}

export default function History() {
  const [items, setItems] = useState<HistoryEntry[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const navigate = useNavigate();

  useEffect(() => {
    fetch("/api/history/list")
      .then((res) => {
        if (!res.ok) throw new Error("Failed to load history");
        return res.json();
      })
      .then((data) => {
        setItems(data);
        setLoading(false);
      })
      .catch((err) => {
        console.error("History fetch error:", err);
        setError("Could not load history");
        setLoading(false);
      });
  }, []);

  const handleDelete = (id: string) => {
    if (window.confirm("Are you sure you want to delete this history record?")) {
      fetch(`/api/history/delete/${id}`, { method: "DELETE" })
        .then((res) => {
          if (!res.ok) throw new Error("Failed to delete history");
          setItems(items.filter((item) => item.id !== id));
        })
        .catch((err) => {
          console.error("History delete error:", err);
          setError("Could not delete history");
        });
    }
  };

  if (loading) {
    return (
      <div className="min-h-dvh flex flex-col">
        <header className="p-4 text-center">
          <h1 className="font-bold">Dogtor AI</h1>
        </header>
        <main className="flex-1 p-4 max-w-2xl mx-auto w-full pb-20">
          <div className="text-sm text-muted-foreground">Loading...</div>
        </main>
        <BottomTabs navigate={navigate} activeTab="history" />
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-dvh flex flex-col">
        <header className="p-4 text-center">
          <h1 className="font-bold">Dogtor AI</h1>
        </header>
        <main className="flex-1 p-4 max-w-2xl mx-auto w-full pb-20">
          <div className="text-sm text-destructive">{error}</div>
        </main>
        <BottomTabs navigate={navigate} activeTab="history" />
      </div>
    );
  }

  if (!items.length) {
    return (
      <div className="min-h-dvh flex flex-col">
        <header className="p-4 text-center">
          <h1 className="font-bold">Dogtor AI</h1>
        </header>
        <main className="flex-1 p-4 max-w-2xl mx-auto w-full pb-20">
          <Card className="text-center py-12">
            <CardHeader>
              <div className="text-6xl mb-4"></div>
              <CardTitle>No History Yet</CardTitle>
            </CardHeader>
            <CardContent>
              <p className="text-muted-foreground text-sm mb-6">
                Run a diagnosis first and it will appear here.
              </p>
              <Button onClick={() => navigate("/")}>
                Start Diagnosis
              </Button>
            </CardContent>
          </Card>
        </main>
        <BottomTabs navigate={navigate} activeTab="history" />
      </div>
    );
  }

  return (
    <div className="min-h-dvh flex flex-col">
      <header className="p-4 text-center">
        <h1 className="font-bold">Dogtor AI</h1>
      </header>

      <main className="flex-1 p-4 max-w-2xl mx-auto w-full pb-20">
        <div className="flex items-center justify-between mb-4">
          <h2 className="text-xl font-semibold">History</h2>
          <Badge variant="secondary">{items.length} cases</Badge>
        </div>

        <div className="space-y-3">
          {items.map((item) => (
            <HistoryCard key={item.id} item={item} navigate={navigate} onDelete={handleDelete} />
          ))}
        </div>
      </main>

      <BottomTabs navigate={navigate} activeTab="history" />
    </div>
  );
}

function HistoryCard({ item, navigate, onDelete }: { item: HistoryEntry; navigate: any; onDelete: (id: string) => void }) {
  let cards: any = null;
  try {
    cards = JSON.parse(item.response);
  } catch (e) {
    console.error("Failed to parse history response:", e);
    cards = null;
  }

  const handleClick = () => {
    if (cards) {
      navigate(`/results/history-${item.id}`, { state: { cards } });
    }
  };

  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    return date.toLocaleDateString('en-US', { 
      month: 'short', 
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  return (
    <Card 
      className={`transition-all ${cards ? 'cursor-pointer hover:shadow-md' : ''}`}
      onClick={cards ? handleClick : undefined}
    >
      <CardContent className="p-4">
        {cards ? (
          <div className="flex items-center justify-between">
            <div className="flex-1 min-w-0">
              <div className="flex items-center gap-2 mb-1">
                <span className="text-xs text-muted-foreground">{formatDate(item.created_at)}</span>
                {cards.diagnosis?.urgency && (
                  <Badge variant="secondary" className="text-xs">
                    {cards.diagnosis.urgency.badge} {cards.diagnosis.urgency.level}
                  </Badge>
                )}
              </div>
              <p className="text-sm font-medium truncate">
                {cards.diagnosis?.likely_condition || "Analysis complete"}
              </p>
              <p className="text-xs text-muted-foreground truncate mt-0.5">
                {item.prompt}
              </p>
            </div>
            <div className="flex items-center gap-2">
              <div className="text-muted-foreground ml-2">
                
              </div>
              <Button
                variant="ghost"
                size="sm"
                onClick={(e) => { e.stopPropagation(); onDelete(item.id); }}
                className="text-destructive hover:text-destructive"
              >
                
              </Button>
            </div>
          </div>
        ) : (
          <div className="space-y-2">
            <div className="text-xs text-muted-foreground">{formatDate(item.created_at)}</div>
            <div className="text-sm font-medium">Raw Data</div>
            <div className="text-xs text-muted-foreground truncate">{item.prompt}</div>
            <Button
              variant="ghost"
              size="sm"
              onClick={(e) => { e.stopPropagation(); onDelete(item.id); }}
              className="text-destructive hover:text-destructive"
            >
              Delete
            </Button>
          </div>
        )}
      </CardContent>
    </Card>
  );
}

